import { State, SetState, sleep, Algorithm } from "../helpers";
import { green,red, orange } from '@material-ui/core/colors';


export const insertionSort = async (state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>) => {
    const data = state.data.slice();
    
    for (let i = 1; i < data.length; i++) {
        let j = i-1;
        let key = data[i];

        while (j >= 0 && data[j] > key) {
            bars[i].style.backgroundColor = orange[400];
            bars[j].style.backgroundColor = red[400];
            data[j + 1] = data[j]
            j -= 1
            await sleep(state.comparisonSleep).then(() => {
                setState((state) => {
                    return {...state, data}
                })
            });
            bars[i].style.backgroundColor = green[400];
            bars[j + 1].style.backgroundColor = green[400];
        }
        data[j + 1] = key
    }
}

export const insertionSortAbout: Algorithm = {
    title: "Insertion Sort",
    timeComplexity: "O(n^2)",
    spaceComplexity: "O(1)",
    link: "https://en.wikipedia.org/wiki/Insertion_sort"
}