import { Link, List, ListItem, ListItemText, Typography } from '@material-ui/core';
import React from 'react';
import {Algorithm} from './helpers';

interface Props {
    title: string;
    algorithms: Algorithm[];
}

const About = (props: Props) => (
    <React.Fragment>
        <Typography variant="h3">{props.title}</Typography>
        <Typography>
            Welcome to my sorting visualiser, unique i know...
        </Typography>
        <Typography>
            As the sorting algorithm works its way through the array, the elements that are being compared will flash red before swaps occur.
        </Typography>
        <br/>
        <Typography>
            The slider controls how big the data set is, as well as the amount of time to sleep between state changes.
        </Typography>
        <br/>
        <Typography>
            The smaller the set, the longer the sleep time. 200 / data.length ms
        </Typography>
        <Typography>
            Orange === i, Red === j where there are two pointers, else just red
        </Typography>
        <List>
            {props.algorithms.map(algorithm => (
                <ListItem>
                    <Link href={algorithm.link}>
                        <ListItemText primary={`${algorithm.title}, time "${algorithm.timeComplexity}" and space "${algorithm.spaceComplexity}"!`}/>
                    </Link>
                </ListItem>
            ))}
        </List>
    </React.Fragment>
)

export default About