import { State, SetState, sleep, Algorithm } from "../helpers";
import { green,red, orange } from '@material-ui/core/colors';

const compareAnimation = async (bars: HTMLCollectionOf<HTMLDivElement>, state: State, i: number, j: number) => {
    bars[i].style.backgroundColor = orange[400];
    bars[j].style.backgroundColor = red[400];
    await sleep(state.comparisonSleep)
    bars[i].style.backgroundColor = green[400];
    bars[j].style.backgroundColor = green[400];
}

// https://en.wikipedia.org/wiki/Quicksort#Hoare_partition_scheme
const partition = async (array: number[], low: number, high: number, state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>) => {
    const middle = Math.floor((high + low) / 2)
    const pivot = array[middle];
    let i = low - 1;
    let j = high + 1
    
    while (true) {
        do {
            i++
            await compareAnimation(bars, state, i, middle)
        } while (array[i] < pivot)

        do {
            j--
            await compareAnimation(bars, state, j, middle)
        } while (array[j] > pivot)

        if (i >= j) {
            return j
        }

        await compareAnimation(bars, state, i, j)

        const temp = array[i]
        array[i] = array[j]
        array[j] = temp
        
        setState((prevState) => {
            return {...prevState, data: array}
        });
    }
}

export const quickSort = async (state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>, low: number = 0, high: number = state.data.length - 1, array?: number[]) => {
    array = array ? array : state.data.slice()
    console.log(`${low} ${high} ${array}`)
    if (low < high) {
        const pivot = await partition(array, low, high, state, setState, bars) as number
        await quickSort(state, setState, bars, low, pivot, array)
        await quickSort(state, setState, bars, pivot + 1, high, array)
    }

    return array;
}

export const quickSortAbout: Algorithm = {
    title: "Quick Sort (Hoare Partition Scheme)",
    timeComplexity: "O(n log n)",
    spaceComplexity: "O(log n)",
    link: "https://en.wikipedia.org/wiki/Quicksort#Hoare_partition_scheme"
}