import { ThemeOptions } from '@material-ui/core';
import { deepPurple, indigo } from '@material-ui/core/colors';

const theme = (dark: boolean = true): ThemeOptions => ({
    palette: {
        type: dark ? 'dark' : 'light',
        primary: {
            main: deepPurple[400]
        },
        secondary: {
            main: indigo[400],
        }
    },
});

export default theme;