import { Box, Button, Container, Tooltip, Slider, Typography } from "@material-ui/core";
import React, { useState } from "react";
import About from "./About";
import {
    bubbleSort,
    bubbleSortAbout,
    insertionSort,
    insertionSortAbout,
    selectionSort,
    selectionSortAbout,
    quickSort,
    quickSortAbout
} from './algorithms';
import { SetState, State } from "./helpers";
import { green } from "@material-ui/core/colors";

const randomIntegers = (amount: number): number[] => {
    const rand = ()  => Math.floor(Math.random() * amount) + 1
    
    return Array(amount).fill(0).map(rand);
}

const about = About({
    title: "Sorting Algorithm Visualiser",
    algorithms: [
        bubbleSortAbout,
        insertionSortAbout,
        selectionSortAbout,
        quickSortAbout
    ]
})

const Sorting = () => {
    const [state, setState] = useState({
        dataSetSize: 50,
        data: randomIntegers(50),
        comparisonSleep: 2,
        sorting: false,
        about
    })

    const handleSlider = (_: any, dataSetSize: number | number[]) => {
        setState({
            ...state,
            comparisonSleep: 200 / (dataSetSize as number),
            dataSetSize: dataSetSize as number
        });

        randomise()
    }

    const randomise = () => {
        setState((prevState) =>({
            ...prevState,
            data: randomIntegers(prevState.dataSetSize)
        }));
    }

    const sort = (func: (state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>) => Promise<any>) => () => {
        // update ref to bars
        const bars =  document.getElementsByClassName('bar') as HTMLCollectionOf<HTMLDivElement>;
            
        setState({...state, sorting: true})

        func(state, setState, bars).then(() => {
            setState((prevState) => {
                return {...prevState, sorting: false}
            })
        });
    }

    return (
        <Container>
            <Box my={1}>
                <Button onClick={randomise} color="primary" variant="contained" disabled={state.sorting}>
                    Randomise
                </Button>
                <Button onClick={sort(bubbleSort)} disabled={state.sorting}>
                    Bubble Sort
                </Button>
                <Button onClick={sort(insertionSort)} disabled={state.sorting}>
                    Insertion Sort
                </Button>
                <Button onClick={sort(selectionSort)} disabled={state.sorting}>
                    Selection Sort
                </Button>
                <Button onClick={sort(quickSort)} disabled={state.sorting}>
                    Quick Sort
                </Button>
                {/* <Button disabled={state.sorting}>
                    Merge Sort
                </Button> */}
                <Slider
                    defaultValue={50}
                    aria-labelledby="dataset-slider"
                    step={1}
                    valueLabelDisplay="auto"
                    max={200}
                    min={10}
                    onChange={handleSlider}
                    disabled={state.sorting}
                />
            </Box>
            <Box boxShadow={2} css={{
                height: 500,
                maxWidth: '100%'
            }}>
                {
                    state.data.map((e, i)  => (
                        <Tooltip title={e}>
                            <div
                                className="bar"
                                key={i}
                                style={{
                                    backgroundColor: green[400],
                                    height: `${(100 / state.data.length) * e}%`,
                                    width: `calc(${100 / state.data.length}% - 1px)`,
                                    marginLeft: '1px',
                                    display: 'inline-block',
                                }}
                            />
                        </Tooltip>
                    ))
                }
            </Box>
            <Box my={2} paddingLeft={5} boxShadow={2}>
                {state.about}
            </Box>
        </Container>
    )
}

export default Sorting