import { State, SetState, sleep, Algorithm } from "../helpers";
import { green,red, orange } from '@material-ui/core/colors';


export const selectionSort = async (state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>) => {
    const data = state.data.slice();
    
    for (let i = 0; i < data.length; i++) {
        let minIndex = i;

        for (let j = i + 1; j < data.length; j++) {
            bars[i].style.backgroundColor = orange[400];
            bars[j].style.backgroundColor = red[400];
            if (data[j] < data[minIndex]) {
                minIndex = j
            }
            await sleep(state.comparisonSleep);
            bars[i].style.backgroundColor = green[400];
            bars[j].style.backgroundColor = green[400];
        }

        if (minIndex !== i) {
            const temp = data[i]
            data[i] = data[minIndex]
            data[minIndex] = temp
        }
        
        setState((state) => {
            return {...state, data}
        })
    }
}

export const selectionSortAbout: Algorithm = {
    title: "Selection Sort",
    timeComplexity: "O(n^2)",
    spaceComplexity: "O(1)",
    link: "https://en.wikipedia.org/wiki/Selection_sort"
}