import { State, SetState, sleep, Algorithm } from "../helpers";
import { green,red, orange } from '@material-ui/core/colors';


export const bubbleSort = async (state: State, setState: SetState, bars: HTMLCollectionOf<HTMLDivElement>) => {
    const data = state.data.slice();

    for (let i = 0; i < data.length; i++) {
        for (let j = i; j < data.length; j++) {
            bars[i].style.backgroundColor = orange[400];
            bars[j].style.backgroundColor = red[400];
            if (data[i] > data[j]) {
                const temp = data[i]
                data[i] = data[j]
                data[j] = temp
            }
            await sleep(state.comparisonSleep).then(() => {
                setState((state) => {
                    return {...state, data}
                })
            });
            bars[i].style.backgroundColor = green[400];
            bars[j].style.backgroundColor = green[400];
        }
    }
}

export const bubbleSortAbout: Algorithm = {
    title: "Bubble Sort",
    timeComplexity: "O(n^2)",
    spaceComplexity: "O(1)",
    link: "https://en.wikipedia.org/wiki/Bubble_sort"
}