import { CssBaseline, useMediaQuery } from '@material-ui/core';
import { deepPurple, indigo } from '@material-ui/core/colors';
import Container from '@material-ui/core/Container';
import Fab from '@material-ui/core/Fab';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import React, { useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import './app.css';
import Navbar from './components/nav/Navbar';
import ScrollToTop from './components/nav/ScrollToTop';
import Home from './components/pages/Home';
import routes from './routes';
import theme from './theme';

const App = () =>  {
  const [state, setState] = useState({
    prefersDarkmode: false,
  });

  const themeProvider = React.useMemo(() => {
    return createMuiTheme(theme(state.prefersDarkmode));
  }, [state.prefersDarkmode])

  return (
    <ThemeProvider theme={themeProvider}>
      <CssBaseline />
      <Navbar links={routes} theme={{
        toggle: () => {
          setState({
            ...state,
            prefersDarkmode: !state.prefersDarkmode
          })
        },
        isDarkmode: state.prefersDarkmode
      }}/>
      <Container>
        <Switch>
          <Route exact path="/">
              <Home/>
          </Route>
          {routes.map(({title, to, page}) => (
            <Route path={to}>
              {page()}
            </Route>
          ))}
        </Switch>
      </Container>
      <ScrollToTop top="#toolbar">
        <Fab color="secondary" size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollToTop>
    </ThemeProvider>
  )
}

export default App
